          transform: rotate(90deg) translate(0, -10px);
}

:global(.bx--cc--threshold--label) {
  background-color: #fa4d56;
  pointer-events: none;
  transition: opacity 0.1s;
  transition-timing-function: cubic-bezier(0.4, 0.14, 0.3, 1);
  display: inline;
  box-shadow: 0 1px 6px 0 rgba(0, 0, 0, 0.2);
  position: absolute;
  word-wrap: break-word;
  z-index: 1059;
  font-family: "IBM Plex Sans Condensed", "Helvetica Neue", Arial, sans-serif;
  color: #f3f3f3;
  line-height: 16px;
  font-size: 12px;
  padding: 4px;
  min-width: 20px;
}

:global(.bx--cc--threshold--label.hidden) {
  opacity: 0;
  transition: opacity 0.1s;
  transition-timing-function: cubic-bezier(0.4, 0.14, 0.3, 1);
}

:global(g.bx--cc--zoom-bar) :global(rect.zoom-bg) {
  fill: #171717;
  stroke: #282828;
}

:global(g.bx--cc--zoom-bar) :global(rect.zoom-slider-bg) {
  fill: #282828;
}

:global(g.bx--cc--zoom-bar) :global(rect.zoom-slider-selected-area) {
  fill: #bebebe;
}

:global(g.bx--cc--zoom-bar) :global(path.zoom-bg-baseline) {
  stroke: #6f6f6f;
  stroke-width: 2;
}

:global(g.bx--cc--zoom-bar) :global(path.zoom-graph-area) {
  fill: #3d3d3d;
  stroke: #6f6f6f;
  stroke-width: 1;
}

:global(g.bx--cc--zoom-bar) :global(path.zoom-graph-area-unselected) {
  fill: #282828;
  stroke: none;
}

:global(g.bx--cc--zoom-bar) :global(g.zoom-bar-brush) :global(rect.handle) {
  fill: #bebebe;
}

:global(g.bx--cc--zoom-bar) :global(g.zoom-bar-brush) :global(rect.handle-bar) {
  fill: #3d3d3d;
}

:global(g.bx--cc--zoom-bar) :global(g.zoom-bar-brush) :global(rect.selection) {
  fill: none;
  stroke: none;
}

:global(.bx--cc--area) :global(path.area),
:global(.bx--cc--area-stacked) :global(path.area) {
  pointer-events: none;
}

:global(g.bx--cc--bubble) :global(circle.dot.hovered) {
  fill-opacity: 1;
  transition: all 0.1s;
  transition-timing-function: cubic-bezier(0.4, 0.14, 0.3, 1);
}

:global(g.bx--cc--bubble) :global(circle.dot.unfilled) {
  fill: #282828;
}

:global(g.bx--cc--bubble) :global(g.lines) :global(path.line) {
  mix-blend-mode: multiply;
}

:global(.bx--cc--line) :global(path.line) {
  pointer-events: none;
  fill: none;
  stroke-width: 1.5;
}

:global(.bx--cc--line) :global(path.line.sparkline-loading) {
  -webkit-animation: shimmer 2.5s infinite linear;
          animation: shimmer 2.5s infinite linear;
}

@-webkit-keyframes -global-shimmer {
  0% {
    stroke: #3d3d3d;
  }
  20% {
    stroke: #ffffff;
    opacity: 0.5;
  }
  100% {
    stroke: #3d3d3d;
  }
}

@keyframes -global-shimmer {
  0% {
    stroke: #3d3d3d;
  }
  20% {
    stroke: #ffffff;
    opacity: 0.5;
  }
  100% {
    stroke: #3d3d3d;
  }
}
:global(g.bx--cc--scatter) :global(circle.dot.hovered) {
  fill-opacity: 1;
  transition: all 0.1s;
  transition-timing-function: cubic-bezier(0.4, 0.14, 0.3, 1);
}

:global(g.bx--cc--scatter) :global(circle.dot.unfilled) {
  fill: #282828;
  stroke-width: 1.5;
}

:global(g.bx--cc--scatter) :global(circle.dot.threshold-anomaly) {
  stroke-width: 3;
}

:global(g.bx--cc--scatter) :global(g.lines) :global(path.line) {
