  stroke-width: 1px;
  stroke: #b28600;
  stroke: none;
}

:global(.bx--cc--meter) :global(rect.value.status--success) {
  fill: #3dbb61;
}

:global(.bx--cc--meter) :global(line.peak) {
  stroke: #f3f3f3;
  stroke-width: 2px;
}

:global(g.bx--cc--scatter-stacked) :global(circle.dot.unfilled) {
  fill: #282828;
  stroke-width: 1.5;
}

:global(g.bx--cc--scatter-stacked) :global(circle.dot.threshold-anomaly) {
