}

:global(.bx--cc--gauge) :global(.gauge-delta-arrow.status--warning) {
  fill: #fdd13a;
}

:global(.bx--cc--gauge) :global(.gauge-delta-arrow.status--success) {
  fill: #3dbb61;
}

:global(svg.bx--cc--chart-svg) {
  font-family: "IBM Plex Sans Condensed", "Helvetica Neue", Arial, sans-serif;
}

:global(svg.bx--cc--chart-svg) :global(text) {
  font-size: 12px;
