  fill: #bebebe;
}

:global(svg.bx--cc--chart-svg) :global(text.meter-title),
:global(svg.bx--cc--chart-svg) :global(text.percent-value) {
  font-size: 16px;
  font-family: "IBM Plex Sans", "Helvetica Neue", Arial, sans-serif;
}

:global(svg.bx--cc--chart-svg) :global(text.meter-title) {
  font-weight: 600;
}

:global(.bx--chart-holder) {
  position: relative;
  display: block;
  width: 100%;
  height: 100%;
  background-color: #171717;
}

:global(.bx--chart-holder) :global(.DONT_STYLE_ME_css_styles_verifier) {
  overflow: hidden;
  opacity: 0;
}

:global(svg.bx--cc--chart-svg) {
  overflow: visible;
}</style>

<main>
    <Header company="SolCharts" platformName="">
        {#if !isCharting}
            <img src="images/solly_icon.png" alt="solly" width="35" height="49" />
        {:else}<img src="images/solly_animated_icon.gif" alt="solly_waving" width="35" height="49" />{/if}
        <div slot="skip-to-content">
            <SkipToContent />
        </div>

        <HeaderNav>
            <HeaderNavItem text="Settings" style="cursor:pointer" href="#/Settings" />
            <HeaderNavItem text="Charts" style="cursor:pointer" href="#/Charts" />
        </HeaderNav>

        <HeaderUtilities>
            <HeaderGlobalAction
                aria-label="Settings"
                icon="{SettingsAdjust20}"
                on:click="{() => {
                    push('#/Settings');
                }}"
            />
            <HeaderGlobalAction
                aria-label="Charts"
                icon="{ChartArea20}"
                on:click="{() => {
                    push('#/Charts');
                }}"
            />
        </HeaderUtilities>
    </Header>

    <Content>
        <Router routes="{routes}" />
    </Content>
</main>